import React, { Component } from 'react';
import { connect } from 'react-redux'
import { Row, Col, List, Avatar, Icon } from 'antd';


const IconText = ({ type, text }) => (
    <span>
        <Icon type={type} style={{ marginRight: 8 }} />
        {text}
    </span>
);

function sortTweet(value){
    return value.substring(0,60) + '...'
}

class Blog extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [{
                href: 'http://ant.design',
                title: `ant design part`,
                avatar: 'https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png',
                description:
                    'Ant Design, a design language for background applications, is refined by Ant UED Team.',
                content:
                    'We supply a series of design principles, practical patterns and high quality design resources (Sketch and Axure), to help people create their product prototypes beautifully and efficiently.',
            },{
                href: 'http://ant.design',
                title: `ant design part ant design part ant design part`,
                avatar: 'https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png',
                description:
                    'Ant Design, a design language for background applications, is refined by Ant UED Team.',
                content:
                    'We supply a series of design principles, practical patterns and high quality design resources (Sketch and Axure), to help people create their product prototypes beautifully and efficiently.',
            }]
        };
    }
    render() {
        return (
            <Row gutter={15}>
                <Col sm={24} md={24} lg={16} xl={20}>
                    <List
                        itemLayout="vertical"
                        dataSource={this.state.data}
                        // footer={
                        //     <div>
                        //         <b>ant design</b> footer part
                        //     </div>
                        // }
                        renderItem={item => (
                            <List.Item
                                key={item.title}
                                actions={[
                                    <IconText type="star-o" text="156" />,
                                    <IconText type="like-o" text="156" />,
                                    <IconText type="message" text="2" />,
                                ]}
                                extra={
                                    <img
                                        width={272}
                                        alt="logo"
                                        src="https://gw.alipayobjects.com/zos/rmsportal/mqaQswcyDLcXyDKnZfES.png"
                                    />
                                }
                            >
                                <List.Item.Meta
                                    title={<a href={item.href} style={{fontSize:'24px'}}>{item.title}</a>}
                                    description={'前端 React'+ '  2019-6-9 14:00:31'}
                                />
                                <p style={{minHeight:'50px'}}>
                                {sortTweet(item.content)}
                                </p>
                            </List.Item>
                        )}
                    />
                </Col>
                <Col xs={24} sm={24} md={24} lg={8} xl={4}>
                    Col
                </Col>
                <style jsx>
                    {
                        `
                        .ant-list-item-main{
                            position: relative;

                        }
                        .ant-list-item-action{
                            position: absolute;
                            bottom: 0;
                        }
                        `
                    }
                </style>
            </Row >
        );
    }
}
function mapStateToProps(state) {
    return state
}
//需要触发什么行为
function mapDispatchToProps(dispatch) {
    return {
        ToggleLogin: (user) => dispatch({ type: 'toggleAuth', user }),
        LoginOut: () => dispatch({ type: 'loginOut' }),
        NoticeSussess: (value) => dispatch({ type: 'success', value }),
        NoticeError: (value) => dispatch({ type: 'error', value }),
        NoticeWarning: (value) => dispatch({ type: 'warning', value }),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Blog);